export default "\n\n\n\n\n#altro {\n    height: 600px;\n    width: 100%;\n    background: url('freedom.jpg');\n    color: white;\n}\n\n\n.main-header {\n    width: 100%;\n    background-color: #2ddf5c;\n    padding: 8px 16px;\n   \n}\n\n\n.main-header > div {\n    display: inline-block;\n    vertical-align: middle;\n}\n\n\n.main-header__brand {\n    color: #0e4f1f;\n    text-decoration: none;\n    font-weight: bold;\n    font-size: 22px;\n\n}\n\n\n.main-nav {\n    display: inline-block;  \n    text-align: right;\n    width: calc(100% - 165px);\n    vertical-align: middle;\n}\n\n\n.main-nav__items{\n    margin: 0;\n    padding: 0 ;\n    list-style: none ;\n}\n\n\n.main-nav__item {\n    display: inline-block;\n    margin: 0 16px;\n}\n\n\n.main-nav__item a {\n    text-decoration: none;\n    color: #0e4f1f;\n    font-weight: bold;\npadding: 3px 0;\n}\n\n\n#my-area {\n    display: inline-block;\n    margin: 0 20px;\n    text-decoration: none;\n    color: #0e4f1f;\n    font-weight: bold;\npadding: 3px 0;\n}\n\n\n#my-area:hover,\n#my-area:active{\n    color: white;\nborder-bottom: 3px solid white;\n}\n\n\n.main-nav__item a:hover,\n.main-nav__item a:active  {\ncolor: white;\nborder-bottom: 3px solid white;\n}\n\n\n.main-nav__item-cta a{\n   color: white;\n   background-color: #ff1b68;\n   padding: 8px 12px;\n   border-radius: 8px;\n}\n\n\n.main-nav__item-cta a:hover,\n.main-nav__item-cta a:active{\n    color: #ff1b68;\n    background: white;\n    border: none;\n}\n\n\n/*\nNAVBAR\n*/\n\n\n.bg-dark {\n    background-color: #0e4071!important;\n}\n\n\n.button__logout{\n    color:  springgreen;\n}\n\n\n.button__logout:hover,\n.button__logout:active{\n    color: #ff1b68;\n}\n\n\n.navbar-brand:hover{\n    color: springgreen;\n}\n\n\n.navbar-dark .navbar-nav .nav-link:hover,\n.navbar-dark .navbar-nav .nav-link:active  {\n    color: springgreen;\n    font-size: 14px;\n    font-weight: bold;\n    border-bottom: 5px solid springgreen;\n    background-color: gray;\n}\n\n\n/*\nNAVBAR\n*/\n\n\n.just_apply{\n    color: springgreen;\n    font-size: 14px;\n    font-weight: bold;\n    border-bottom: 5px solid springgreen;\n \n}\n\n\n/*\nICON MENU\n*/\n\n\n.icon-bar {\n\twidth: 22px; \n\theight: 2px;\n\tbackground-color: white;\n\tdisplay: block;\n\ttransition: all 0.2s;\n\tmargin-top: 4px\n}\n\n\n.navbar-toggler {\n  border: none;\n  background: transparent !important;\n}\n\n\n.navbar-toggler .top-bar {\n    transform: rotate(45deg);\n    transform-origin: 10% 10%;\n  }\n\n\n.navbar-toggler .middle-bar {\n    opacity: 0;\n  }\n\n\n.navbar-toggler .bottom-bar {\n    transform: rotate(-45deg);\n    transform-origin: 10% 90%;\n  }\n\n\n.navbar-toggler.collapsed .top-bar {\n    transform: rotate(0);\n  }\n\n\n.navbar-toggler.collapsed .middle-bar {\n    opacity: 1;\n  }\n\n\n.navbar-toggler.collapsed .bottom-bar {\n    transform: rotate(0);\n  }\n\n\n/*\n/ICON MENU\n*/\n\n\n/*# sourceMappingURL=data:application/json;base64, */"