export default "\n\n\n\n\n\n\n\n#product-overview {\n    background: url('freedom.jpg');\n    width: 100%;\n    height: 528px;\n    padding: 10px;   \n}\n\n\n.main-header {\n    width: 100%;\n    background-color: #2ddf5c;\n    padding: 8px 16px;\n   \n}\n\n\n.main-header > div {\n    display: inline-block;\n    vertical-align: middle;\n}\n\n\n.main-header__brand {\n    color: #0e4f1f;\n    text-decoration: none;\n    font-weight: bold;\n    font-size: 22px;\n\n}\n\n\n.main-nav {\n    display: inline-block;  \n    text-align: right;\n    width: calc(100% - 75px);\n    vertical-align: middle;\n}\n\n\n.main-nav__items{\n    margin: 0;\n    padding: 0 ;\n    list-style: none ;\n}\n\n\n.main-nav__item {\n    display: inline-block;\n    margin: 0 16px;\n}\n\n\n.main-nav__item a {\n    text-decoration: none;\n    color: #0e4f1f;\n    font-weight: bold;\npadding: 3px 0;\nvertical-align: middle;\n}\n\n\n.main-nav__item a:hover,\n.main-nav__item a:active  {\ncolor: white;\nborder-bottom: 3px solid white;\n}\n\n\n.main-nav__item-cta a{\n   color: white;\n   background-color: #ff1b68;\n   padding: 8px 16px;\n   border-radius: 8px;\n}\n\n\n.main-nav__item-cta a:hover,\n.main-nav__item-cta a:active{\n    color: #ff1b68;\n    background: white;\n    border: none;\n}\n\n\n/*\npLan \n*/\n\n\n.plan{\n    background: #d5ffdc;\n    text-align: center;\n    padding: 16px;\n    margin: 8px;\n    display: inline-block;\n    width: 30%;\n}\n\n\n.plan--highlited{\n    background: #19b84c;\n    color: white;\n    box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.5);\n}\n\n\n.plan__annotation{\n    background: white;\n    color: #19b84c;\n    padding: 8px;\n    box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.5);\n    border-radius: 4px;\n}\n\n\n.plan__features{\nlist-style: none;\npadding: 0;\nmargin: 0;\n}\n\n\n.plan__feature{\n margin: 8px 0;\n}\n\n\n.plan--highlited .plan__title {\n    color: white;\n}\n\n\n.plan__price {\n    color: #0e4f1f;\n}\n\n\n.button {\n    background:#0e4f1f ;\n    color: white;